import React, { Component } from "react";
import Total from "./components/total/Total";
import History from "./components/history/History";
import Operation from "./components/operation/Operation";

class App extends Component {
  state = {
    transactions: [],
    description: "",
    amount: "",
    moneyIncome: 0,
    moneyExpenses: 0,
    balance: 0,
  };

  getBalance() {
    const balance = this.state.moneyIncome - this.state.moneyExpenses;
    this.setState({ balance });
  }

  addTransaction = (add) => {
    const transactions = [...this.state.transactions];

    const transaction = {
      id: `cmr${(+new Date()).toString(16)}`,
      description: this.state.description,
      amount: +this.state.amount,
      add,
    };

    transactions.push(transaction);

    this.setState({ transactions, description: "", amount: "" }, () => {
      this.calculation(true);
      this.calculation(false);
    });
  };

  calculation(type) {
    const calc = this.state.transactions.reduce(
      (bal, item) => (item.add === type ? bal + item.amount : bal),
      0
    );

    if (type) {
      this.setState({ moneyIncome: calc }, this.getBalance);
    } else {
      this.setState({ moneyExpenses: calc }, this.getBalance);
    }
  }

  addDescription = (e) => {
    this.setState({ description: e.target.value });
  };

  addAmount = (e) => {
    this.setState({ amount: e.target.value });
  };

  render() {
    return (
      <>
        <header>
          <h1>Кошелек</h1>
          <h2>Калькулятор расходов</h2>
        </header>

        <main>
          Operation
          <div className="container">
            <Total
              balance={this.state.balance}
              moneyIncome={this.state.moneyIncome}
              moneyExpenses={this.state.moneyExpenses}
            />

            <History transactions={this.state.transactions} />

            <Operation
              addTransaction={this.addTransaction}
              addAmount={this.addAmount}
              addDescription={this.addDescription}
              description={this.state.description}
              amount={this.state.amount}
            />
          </div>
        </main>
      </>
    );
  }
}

export default App;
